import PropTypes from 'prop-types';
import { Fragment } from 'react';

import Copyable from './Copyable';
import Swatch from './Swatch';
import {
  Group,
  GroupHeader,
  GroupTitle,
  GroupTable,
  RowHeading,
  Cell,
  CellContents,
  Description,
} from './Table';

const Palette = ({ palette }) => {
  const groups = Object.entries(palette).map(([group, values]) => ({
    group,
    values: Object.entries(values).map(([token, value]) => ({
      token,
      description: value,
      value: value.toUpperCase(),
    })),
  }));

  return (
    <>
      {groups.map(({ group, values }, i) => (
        <Group key={`group-${i}`}>
          <GroupHeader>
            <GroupTitle>{group}</GroupTitle>
          </GroupHeader>
          <GroupTable colWidth={20}>
            <tbody>
              {values.map(({ token, description, value }, j) => (
                <tr key={`variant-${j}`}>
                  <RowHeading>
                    {token} <Description>{description}</Description>
                  </RowHeading>
                  <Cell>
                    <CellContents>
                      <Copyable value={value}>
                        <Swatch fill={value} width={24} height={24} />
                      </Copyable>
                    </CellContents>
                  </Cell>
                </tr>
              ))}
            </tbody>
          </GroupTable>
        </Group>
      ))}
    </>
  );
};

Palette.propTypes = {
  palette: PropTypes.object.isRequired,
};

export default Palette;
